import { RegisterComponent } from '@/common/register-component';
import { NylasSchedulerConfigConnector } from '@/connector/nylas-scheduler-config-connector';
import { debug } from '@/utils/utils';
import { AttachInternals, Component, Host, State, h, Element, Prop, Watch, Event, EventEmitter, Listen } from '@stencil/core';
import { NylasSchedulerEditor } from '../nylas-scheduler-editor/nylas-scheduler-editor';
import { Configuration } from '@nylas/core';

/**
 * The `nylas-min-cancellation-notice` component is a form input for the description of an event.
 * @part nmcn - The minimum cancellation notice container
 * @part nmcn__number-dropdown - The number dropdown container
 * @part nmcn__number-dropdown-button - The number dropdown button
 * @part nmcn__number-dropdown-content - The number dropdown content
 * @part nmcn__period-dropdown - The period dropdown container
 * @part nmcn__period-dropdown-button - The period dropdown button
 * @part nmcn__period-dropdown-content - The period dropdown content
 */
@Component({
  tag: 'nylas-min-cancellation-notice',
  styleUrl: 'nylas-min-cancellation-notice.scss',
  shadow: true,
  formAssociated: true,
})
export class NylasMinCancellationNotice {
  /**
   * The selected config
   */
  @Prop() selectedConfiguration?: Configuration;
  /**
   * The minimum cancellation notice stored in the configuration
   */
  @Prop() minCancellationNotice?: number;
  /**
   * The element internals.
   */
  @AttachInternals() internals!: ElementInternals;
  /**
   * The host element <nylas-min-cancellation-notice>
   */
  @Element() host!: HTMLElement;
  /**
   * The name of the min cancellation notice input.
   */
  @Prop() name: string = 'min-cancellation-notice';

  /**
   * The selected number of minutes for the min cancellation notice.
   */
  @State() selectedNumber: number = 1;
  /**
   * The selected period for the min cancellation notice.
   */
  @State() selectedPeriod: string = 'day';

  /**
   * When a name prop is passed, stencil does not automatically set the name attribute on the host element.
   * Since this component is form-associated, the name attribute is required for form submission.
   * This is a workaround to ensure that the name attribute is set on the host element.
   */
  @Watch('name')
  elementNameChangedHandler(newValue: string) {
    debug('nylas-min-cancellation-notice', 'elementNameChangedHandler', newValue);
    this.host.setAttribute('name', newValue);
  }

  @Watch('selectedConfiguration')
  selectedConfigurationChangedHandler(newVal: Configuration) {
    const minCancellationNotice = newVal?.scheduler?.min_cancellation_notice;
    if (minCancellationNotice) {
      this.updateNumberAndPeriodFromMinutes(minCancellationNotice);
    }
  }

  @Watch('availableDaysInFuture')
  availableDaysInFutureChanged(newValue: number) {
    this.updateNumberAndPeriodFromMinutes(newValue);
  }

  @Event() valueChanged!: EventEmitter<{
    value: string;
    name: string;
  }>;

  connectedCallback() {
    debug('nylas-min-cancellation-notice', 'connectedCallback');
  }

  componentWillLoad() {
    debug('nylas-min-cancellation-notice', 'componentWillLoad');
    this.host.setAttribute('name', this.name);
  }

  componentDidLoad() {
    debug('nylas-min-cancellation-notice', 'componentDidLoad');
    this.availableDaysInFutureChanged(this.minCancellationNotice ?? 1);
    if (typeof this.internals.setFormValue !== 'function') {
      return;
    }
    this.internals.setFormValue(this.minCancellationNotice?.toString() ?? '1', this.name);
  }

  disconnectedCallback() {
    debug('nylas-min-cancellation-notice', 'disconnectedCallback');
  }

  @Listen('timePeriodChanged')
  timePeriodChangedHandler(
    event: CustomEvent<{
      number: number;
      period: string;
    }>,
  ) {
    const { number, period } = event.detail;
    let totalMinutes;
    const minutesInHour = 60;
    const minutesInDay = minutesInHour * 24;
    const minutesInWeek = minutesInDay * 7;
    const minutesInMonth = minutesInDay * 30; // Assuming 30 days in a month for simplification

    if (period === 'minute') {
      totalMinutes = number;
    } else if (period === 'hour') {
      totalMinutes = number * minutesInHour;
    } else if (period === 'day') {
      totalMinutes = number * minutesInDay;
    } else if (period === 'week') {
      totalMinutes = number * minutesInWeek;
    } else if (period === 'month') {
      totalMinutes = number * minutesInMonth;
    } else {
      totalMinutes = 0;
    }
    this.valueChanged.emit({ value: totalMinutes, name: 'minCancellationNotice' });
    if (typeof this.internals.setFormValue !== 'function') {
      return;
    }
    this.internals.setFormValue(totalMinutes.toString(), this.name);
  }

  private updateNumberAndPeriodFromMinutes(minutes: number): void {
    const minutesInHour = 60;
    const minutesInDay = minutesInHour * 24;
    const minutesInWeek = minutesInDay * 7;
    const minutesInMonth = minutesInDay * 30; // Assuming 30 days in a month for simplification
    let number, period;
    if (minutes >= minutesInMonth) {
      number = Math.floor(minutes / minutesInMonth);
      period = 'month';
    } else if (minutes >= minutesInWeek) {
      number = Math.floor(minutes / minutesInWeek);
      period = 'week';
    } else if (minutes >= minutesInDay) {
      number = Math.floor(minutes / minutesInDay);
      period = 'day';
    } else if (minutes >= minutesInHour) {
      number = Math.floor(minutes / minutesInHour);
      period = 'hour';
    } else {
      number = minutes;
      period = 'minute';
    }
    this.selectedNumber = number;
    this.selectedPeriod = period;
  }

  @RegisterComponent<NylasMinCancellationNotice, NylasSchedulerConfigConnector, Exclude<NylasSchedulerEditor['stores'], undefined>>({
    name: 'nylas-min-cancellation-notice',
    stateToProps: new Map([['schedulerConfig.selectedConfiguration', 'selectedConfiguration']]),
    eventToProps: {},
    fireRegisterEvent: true,
  })
  render() {
    return (
      <Host>
        <div class="nylas-min-cancellation-notice" part="nmcn">
          <label>
            Minimum cancellation notice
            <span class="label-icon">
              <tooltip-component>
                <info-icon slot="tooltip-icon" />
                <span slot="tooltip-content">Set the minimum amount of time required to cancel an event.</span>
              </tooltip-component>
            </span>
          </label>
          <time-period-selector
            id="min-cancellation-notice"
            exportparts="tps__number-dropdown: nmcn__number-dropdown, tps__number-dropdown-button: nmcn__number-dropdown-button, tps__number-dropdown-content: nmcn__number-dropdown-content, tps__period-dropdown: nmcn__period-dropdown, tps__period-dropdown-button: nmcn__period-dropdown-button, tps__period-dropdown-content: nmcn__period-dropdown-content"
            timePeriods={['minute', 'hour', 'day', 'week']}
            defaultSelectedNumber={this.selectedNumber}
            defaultSelectedPeriod={this.selectedPeriod}
          />
        </div>
      </Host>
    );
  }
}
